*, *:before, *:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #1D1D1D;
}

/* Reset button styles */
button {
  padding: 4px 8px;
  border: 1px solid #1D1D1D;
  border-radius: 4px;
  font: inherit;
  color: inherit;
  background-color: white;
  cursor: pointer;
  transition: background-color 0.2s;
}

button:not([disabled]):hover {
  background-color: #E0E0E0;
}

button[disabled] {
  border-color: #E0E0E0;
  color: #E0E0E0;
  cursor: not-allowed;
}